.container_details {
  display: flex;
  flex-direction: column;
  padding: 10px;
  gap: 20px;
}
.details_page > img {
  width: 200px;
  height: 300px;
  display: block;
}
.genres_wrapp {
  display: flex;
  gap: 5px;
}
.details_page {
  display: flex;
  padding: 10px;
  gap: 20px;
}
.details_list {
  display: flex;
  gap: 20px;
  list-style: none;
  margin-left: 15px;
}
/* ===========================, */
.btn_back {
  display: block;
  margin-left: 20px;
  color: #007bff;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.btn_back:hover {
  color: #0056b3;
  text-decoration: underline;
}

.btn_back:visited {
  color: #800080;
}

.btn_back:focus {
  outline: none;
}
.links_details {
  display: block;
  margin-left: 10px;
}
